import React from "react";
import { Card, Col, Form, Row } from "react-bootstrap";
import { MdOutlineDeleteForever } from "react-icons/md";

function Questionnaires() {
  return (
    <>
      <h5>Questionnaires</h5>
      <Card className="shadow-sm vh-100">
        <Form>
          <Row className="m-3 d-flex justify-content-center flex-column">
            <Col
              xl={7}
              lg={10}
              xs={12}
              className="p-3 rounded mx-auto mb-3"
              style={{
                boxShadow: "0px 0px 24px 0px #0000000A",
                borderTop: "6px solid #6045EB",
              }}
            >
              <Form.Group className="mb-3" controlId="formTitle">
                <Form.Label className="label-primary mt-2">
                  Enter Form Title
                </Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Form Title"
                  maxLength={250}
                />
              </Form.Group>
            </Col>
            <Col
              xl={7}
              lg={10}
              xs={12}
              className="p-3 rounded mx-auto mb-3"
              style={{
                boxShadow: "0px 0px 24px 0px #0000000A",
                borderLeft: "6px solid #3F8BFC",
              }}
            >
              <Form.Group className="mb-3" controlId="formQuestion1">
                <Form.Label className="label-primary mt-2">
                  Enter Question
                </Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Question"
                  maxLength={250}
                />
                <Form.Control
                  as="textarea"
                  rows={3}
                  placeholder="Answer Text"
                  className="mt-2"
                  disabled={true}
                />
              </Form.Group>
              <div className="w-100 d-flex justify-content-end">
                <MdOutlineDeleteForever
                  size={26}
                  color="#999999"
                  className="mx-3"
                  style={{borderRight: '2px solid #999999'}}
                />
                <Form.Check
                  type="switch"
                  id="custom-switch"
                  label={<span style={{ color: "#FF3A3A" }}>Required*</span>}
                />
              </div>
            </Col>
          </Row>
        </Form>
      </Card>
    </>
  );
}

export default Questionnaires;
